@import url(https://unpkg.com/tailwindcss@^2/dist/tailwind.min.css);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
.cursive {
  font-family: Amatic SC, cursive;
}
.montserrat {
  font-family: Montserrat, sans-serif;
}
* {
  -webkit-tap-highlight-color: transparent;
}
.aboutbg {
  background-image: url(/personal_site/static/media/about.b3d0bd65.jpg);
  max-width: 100%;
  height: auto;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
video {
  height: 100vh;
  min-height: 100%;
}
.gallerybg {
  background-image: url(/personal_site/static/media/gallerybg.398600c6.jpg);
  max-width: 100%;
  height: auto;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.g-img {
  width: 100%;
  border-bottom: 1px solid black;
  padding: 20px;
}
button {
  outline: none;
  border: none;
}
button:focus {
  outline: 0;
}
.homebutton :hover {
  padding-bottom: 50px !important;
}
.playfair {
  font-family: 'Playfair Display', serif;
}
.arrow {
  border: solid #1f2937;
  border-width: 0 4px 4px 0;
  display: inline-block;
  padding: 4px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.aboutbutton:hover {
  border-bottom: 1px solid black;
}
.aboutbutton {
  transition: 0.5s ease-in-out;
}

